html {
	font-size: 62.5%;

	@include maxW(1280px) {
		font-size: 56.25%;
	}

	@include maxW(1023px) {
		font-size: 50%;
	}

	@include maxW(767px) {
		font-size: 43.75%;
	}
}

body {
	font-family: "Rubik", sans-serif;
	color: white;
	background-color: $color-black;
}

.container {
	max-width: 125rem;
	padding: 0 2rem;
	margin: 0 auto;
}

section {
	padding: 9rem 0;
}

.text-center {
	text-align: center;
}

.mb-lg {
	margin-bottom: 10rem !important;
}

.mb-md {
	margin-bottom: 5rem !important;
}

.mr-btn {
	margin-right: 3rem;
}

.mb-sm {
	margin-bottom: 3rem !important;
}

header,
footer,
main > * {
	display: none;
}

.wave {
	width: 100%;
	display: block;
}
