.header {
	padding: 2rem;
	position: absolute;
	z-index: 2;
	top: 0;
	width: 100%;
	background-color: transparent;

	.logo {
		transform: translateX(-20%);
	}
	.nav {
		display: flex;
		justify-content: space-between;
		align-items: center;

		&__links {
			display: flex;
			align-items: center;
			gap: 1rem;
			color: white;
		}
		&__link {
			padding: 1rem 1.5rem;
			font-size: 1.4rem;
			transition: all 0.2s;
			border: 1px solid transparent;

			&:not(:last-child):hover {
				border: 1px solid white;
				border-radius: 10rem;
			}
		}

		&__icons {
			display: flex;
			align-items: center;
			gap: 2rem;
		}

		&__icon {
			cursor: pointer;
			font-size: 1.7rem;
			fill: white;
			background-color: rgba($color: white, $alpha: 0.2);
			padding: 5px;
			border-radius: 50%;
			border: 1px solid transparent;
			transition: all 0.2s;

			&:hover {
				border: 1px solid white;
			}
		}
	}

	.active {
		border: 1px solid white;
		border-radius: 10rem;
	}

	display: block;
	@include maxW(1023px) {
		.nav {
			&__links {
				display: none;
			}
			.logo {
				transform: translateX(0%);
			}
		}
	}

	@include maxW(767px) {
		padding: 2rem 0;
		.nav {
			&__icons {
				gap: 5px;
			}

			&__icon {
				font-size: 1.3rem;
				padding: 2px;
			}
		}
	}
}
